import { mapState, mapMutations } from 'vuex';
export default {
	data() {
		return {
			currentBrand:null,
			brands:[],
			products:[]
		}
	},
	onLoad() {
		
		uni.request({
			url:'http://39.98.107.68:8000/Api/M_Brand',
			dataType:'json',
			success:res=>{
				if(res.data && res.data.length > 0){
					this.brands = res.data;
					this.currentBrand = this.brands[0];
					this.$store.commit("setSelectBrand", this.brands[0]);
				}
			}
		})
	},
	watch:{
		currentBrand(value){
			uni.showLoading({
				title:"数据加载中"
			})
			uni.request({
				url:'http://39.98.107.68:8000/Api/M_DeviceModel',
				dataType:'json',
				success:res=>{
					this.products = [];
					if(res.data && res.data.length > 0){
						res.data.forEach(x=>{
							if(x.BrandId == value.Id){
								this.products.push(x)
							}
						})
						uni.hideLoading()
					} 
				}
			})
		}
	},
	methods: {
		...mapMutations(["setSelectBrand", "setSelectProduct"]),
		selectItem(brand){
			this.currentBrand = brand;
			this.$store.commit("setSelectBrand", brand);
		},
		search(){
			uni.navigateTo({
				url:"./searchDevice?brandid=" + this.currentBrand.Id
			})
		},
		selectProduct(product){
			this.$store.commit("setSelectProduct", product)
			uni.navigateTo({
				url:"./searchDevice"
			})
		}
	}
}
